.experimentsGroup {
    padding: 64px 96px 96px 96px;
    background: #000;
    border: 1px solid black;

}

.experimentsCardGroup {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 64px;
    grid-row-gap: 64px;
    margin-top: 96px;
}

.experimentsGroup .infoLinkGroup {
    margin-top: 96px;
    display: flex;
    justify-content: center;
}

.experimentsGroup .infoLink {
    background: rgba(255,255,255,0.1);
    color: #fff;
}

.experimentsGroup .infoLink:hover {
    background: rgba(255,255,255,1);
    color: #000;
}

.experimentsGroup .sectionName {
    color: #fff;
}



@media (max-width: 1024px) {
    .experimentsCardGroup {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 768px) {
    .experimentsGroup {
        padding: 64px;
    }
    .experimentsCardGroup {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 414px) {
    .experimentsGroup {
        padding: 32px;
    }
    .experimentsCardGroup {
        grid-template-columns: 1fr;
        grid-row-gap: 32px;
        margin-top: 64px;
    }
}