.petProjectCoverWrap {
    width: 100%;
    height: 260px;
    background: transparent;
    position: relative;
    overflow: hidden;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;
    box-shadow: 0px 24px 32px rgba(0,0,0,0.1);
    transition: 0.5s ease-in-out;
}

.petProjectCoverWrap:hover {
    box-shadow: 0px 48px 64px rgba(0,0,0,0.3);
    transform: scale(1.05,1.05);
    cursor: pointer;
}

.petProjectCover {
    width: 100%;
    height: 100%;
    position: absolute;
    transition: 1s ease-in-out;
}

.petProjectCover:hover {
    transform: scale(1.1,1.1);
}