.cardGroup {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 96px 0px;
    grid-column-gap: 64px;
    align-items: center;
    position: relative;
}

.cardGroup:nth-child(even) .cardImageWrap {
    order: 2;
}

.cardGroup:nth-child(even) .infoGroup {
    order: 1;
}

.cardGroup .cardImageWrap{
    width: 100%;
    height: 100%;
    background: transparent;
    border-radius: 16px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    box-shadow: 0px 24px 32px rgba(0,0,0,0.1);
    transition: 0.5s ease-in-out;
}

.cardGroup .cardImage {
    width: 100%;
    height: 400px;
    transition: 1s ease-in-out;
}

.companyName {
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 800;
    letter-spacing: 3px;
    color: rgba(0, 0, 0, 0.5);
    margin-bottom: 16px; 
}

