    width: 100%;
    position: absolute;
    transition: 1s ease-in-out;
    display: block;
} */



.cardImageWrap{
    transition: 0.5s ease-in-out;
    background: transparent;
}

.cardGroup .cardImageWrap:hover {
    cursor: pointer;
    box-shadow: 0px 48px 64px rgba(0,0,0,0.3);
    transform: scale(1.05,1.05);
}

.cardGroup .cardImage:hover {
    transform: scale(1.1,1.1);
    background-size: 210%;
}

@media (max-width: 768px) {

    .cardGroup:nth-child(even) .cardImageWrap {
        order: 1;
    }

    .cardGroup {
        grid-template-columns: 1fr;
    }

    .companyName {
        margin-top: 32px;
        color: rgba(0, 0, 0, 0.3);
        font-size: 16px;
    }

}

@media (max-width: 414px) {

    .cardGroup:nth-child(even) .cardImage {
        order: 1;
    }

    .cardGroup {
        grid-template-columns: 1fr;
    }

    .cardGroup .cardImage {
        height: 240px;
    }

    .companyName {
        font-size: 12px;
        margin-top: 24px;
        color: rgba(0, 0, 0, 0.3);
    }

    .cardGroup {
        margin-top: 64px;
        margin-bottom: 64px;
    }

}

