html {
  overflow-y: scroll;
  scroll-behavior: smooth;
}

body {
  margin: 0px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}

.sectionName {
  font-size: 40px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 6px;
}

h1 {
  font-size: 40px;
  font-weight: 800;
  margin: 0px;
}

p {
  color: rgba(0,0,0,0.5);
  font-size: 20px;
  font-weight: 400;
  line-height: 150%;
}

.infoLinkGroup {
  display: flex;
}

.infoLinkGroup a {
  font-size: 20px;
  line-height: 56px;
  background: rgba(0, 0, 0, 0.1);
  padding: 0px 32px;
  border-radius: 28px;
  font-weight: 400;
  color: rgba(0,0,0,1);
  text-decoration: none;
  margin-top: 8px;
  transition: 0.3s ease-in-out;
}

.infoLink {
  font-size: 20px;
  line-height: 56px;
  background: rgba(0, 0, 0, 0.1);
  padding: 0px 32px;
  border-radius: 28px;
  font-weight: 400;
  color: rgba(0,0,0,1);
  text-decoration: none;
  margin-top: 8px;
  transition: 0.3s ease-in-out;
}

.infoLinkGroup a:hover {
  cursor: pointer;
  color: rgba(255,255,255,1);
  background: rgba(0, 0, 0, 1);
}

.infoLink:hover {
  cursor: pointer;
  color: rgba(255,255,255,1);
  background: rgba(0, 0, 0, 1);
}


@media (max-width: 414px) {
  p {
    font-size: 16px;
  }
  h1 {
    font-size: 32px;
  }

  .infoLink, .infoLinkGroup a {
    font-size: 16px;
    width: 100%;
    text-align: center;

  }

  .sectionName {
    font-size: 32px;
  }

}