.layoutGroup {
    /* background: green; */
    padding-top: 176px;
    padding-bottom: 96px;
    /* margin: 0px 96px 0px 96px; */
    padding-left: 96px;
    padding-right: 96px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);  
    grid-column-gap: 64px;
    color: #fff;
}

.layoutGroup h2 {
    font-size: 32px;
    grid-column: 1/-1;
    margin-top: 64px;
}

.layoutGroup h3 {
    font-size: 20px;
    grid-column: 1/-1;
    margin: 0;
    padding: 0;
    margin-top: 24px;
    margin-bottom: 24px;
}

.layoutGroup p {
    grid-column: 1/3;
    color: rgba(255,255,255,0.6);
    margin-top: 0px;
}

.layoutGroup .projectImg {
    grid-column: 1/3;
    width: 100%;
    height: 400px;
    /* background: rgb(0, 255, 0); */
    background-size: cover;
    background-position: center;
    border-radius: 16px;
    border-width: 0;
    margin: 16px 0 32px 0;
}

.layoutGroup .note {
    grid-column: 3;
    font-size: 16px;
    line-height: 150%;
    color: rgba(255,255,255,0.4);
    font-style: italic;
    margin-top: 16px;
}

.layoutGroup ul {
    grid-column: 1/3;
    font-size: 20px;
    color: rgba(255,255,255,0.6);
    line-height: 150%;
    padding: 0;
    margin: 0;
}

.layoutGroup li {
    margin-bottom: 16px;
}

.layoutWrap {
    background: #000;
}

@media (max-width: 1024px) {

    .layoutGroup {
        padding-left: 32px;
        padding-right: 32px;
        grid-column-gap: 32px;
        padding-top: 120px;
    }

    .layoutGroup .projectImg {
        grid-column: 1/4;
        height: 480px;
    }
    .layoutGroup p {
        grid-column: 1 / 4;
    }
    .layoutGroup .note {
        grid-column: 4;
    }
    .layoutGroup ul {
        grid-column: 1/4;
    }
}

@media (max-width: 768px) {
    .layoutGroup {
        padding-left: 32px;
        padding-right: 32px;
        grid-column-gap: 32px;
        padding-top: 120px;
    }
    .layoutGroup .projectImg {
        grid-column: 1/4;
        height: 320px;
    }
    .layoutGroup p {
        grid-column: 1 / 4;
    }
    .layoutGroup .note {
        grid-column: 4;
    }
    .layoutGroup ul {
        grid-column: 1/4;
    }
    .layoutGroup h2 {
        grid-column: 1 / -1;
        margin-top: 32px;
        /* margin-bottom: 24px; */
    }
}

@media (max-width: 414px) {
    .layoutGroup {
        padding-left: 32px;
        padding-right: 32px;
        grid-column-gap: 0;
        padding-top: 96px;
    }

    .layoutGroup .projectImg {
        grid-column: 1 / -1;
        height: 240px;
        margin-bottom: 8px;
    }
    .layoutGroup h2 {
        grid-column: 1 / -1;
        margin-top: 32px;
        margin-bottom: 24px;
    }
    .layoutGroup h3 {
        grid-column: 1 / -1;
    }
    .layoutGroup p {
        grid-column: 1 / -1;
    }
    .layoutGroup .note {
        grid-column: 1 / -1;
        font-size: 14px;
        margin-top: 8px;
        margin-bottom: 24px;
    }

    .layoutGroup ul {
        grid-column: 1/-1;
        font-size: 16px;
    }

    .layoutGroup li {
        margin-bottom: 24px;
    }
}