.conceptLinkWrap {
    width: 100%;
    background: transparent;
    height: 100%;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    box-shadow: 0px 24px 32px rgba(0,0,0,0.1);
    transition: 0.5s ease-in-out;
}

.conceptGroup {
    height: 180px;
}

.conceptLinkWrap:hover {
    cursor: pointer;
    transform: scale(1.05,1.05);
}

.conceptLink {
    width: 100%;
    height: 100%;
    position: absolute;
    transition: 1s ease-in-out;
}

.conceptLink:hover {
    transform: scale(1.1,1.1);
}



@media (max-width: 768px) {
    .conceptGroup {
        height: 240px;
    }
}

@media (max-width: 414px) {
    .conceptGroup {
        height: 240px;
    }
}