.headGroup {
    /* background: yellow; */
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 64px;
}

.headGroup h1 {
    grid-column: 1 / -1;
    margin-bottom: 44px;
}

.headGroup .infoGroup {
    grid-column: 4;
}

.headGroup .headCover {
    height: 400px;
    width: 100%;
    /* background: rgba(0,0,0,0.1); */
    grid-column: 1/4;
    border-radius: 16px;
    border-color: rgba(0,0,0,0.05);  
    border-width: 1px;
    border-style: solid;
}

.headGroup .infoTitle {
    font-size: 18px;
    font-weight: 800;
    letter-spacing: 3px;
    margin-bottom: 8px;
    text-transform: uppercase;
}

.headGroup .infoDesc {
    font-size: 20px;
    margin-bottom: 38px;
    color: rgba(255,255,255,0.6);
}

@media (max-width: 1024px) {
    .headGroup {
        grid-column-gap: 32px;
    }
    .headGroup .headCover {
        height: 400px;
        grid-column: 1/-1;
    }

    .headGroup .infoGroup {
        grid-column: 1/-1;
        margin-top: 48px;
    }
}

@media (max-width: 768px) {
    .headGroup {
        grid-column-gap: 32px;
    }
    .headGroup .headCover {
        height: 320px;
        grid-column: 1/-1;
    }

    .headGroup .infoGroup {
        grid-column: 1/-1;
        margin-top: 48px;
    }
}

@media (max-width: 414px) {
    
    .headGroup .headCover {
        height: 240px;
        grid-column: 1 / -1;
    }

    .headGroup .infoGroup {
        grid-column: 1 / -1;
        margin-top: 32px;
    }

    .headGroup .infoTitle {
        font-size: 16px;
    }

    .headGroup .infoDesc {
        font-size: 16px;
        margin-bottom: 16px;
    }

    .headGroup h1 {
        margin-bottom: 32px;
    }

}