.welcomeGroup {
    background: black;
    color: white;
    height: 100vh;
    padding: 64px 96px;
    display: grid;
    z-index: 100;
}

.welcomeGroup a {
    text-decoration: none;
    font-size: 20px;
    margin: 0 8px;
    font-weight: 300;
    color: rgba(255,255,255,0.5);
    transition: 0.5s;
    text-transform: lowercase;
}

.welcomeGroup a:hover {
    color: rgba(255,255,255,1);
    cursor: pointer;
}

.welcomeMessage {
    align-self: center;
}

.welcomeMessage h1 {
    font-size: 48px;
    /* text-transform: uppercase; */
    /* letter-spacing: 10px; */
    font-weight: 300;
    max-width: 960px;
    line-height: 150%;
    margin: 0px;
}

.welcomeMessage p {
    font-size: 32px;
    font-weight: 300;
    /* margin-right: 20%; */
    width: 800px;
    line-height: 150%;
    color: rgba(255,255,255,0.5);
}

@media (max-width: 768px) {
    .welcomeMessage p {
        width: 100%;
    }
    .welcomeGroup {
        padding: 64px;
    }
}

@media (max-width: 414px) {

    .welcomeGroup {
        padding: 32px;
    }
    .welcomeMessage p {
        width: 100%;
        font-size: 16px;
        font-weight: 400;
    }
    .welcomeMessage h1 {
        font-size: 32px;
    }

    .welcomeGroup a {
        font-size: 16px;
        font-weight: 400;
    }

}